*{
  font-family: Arial, Helvetica, sans-serif;
}
body{
  padding: 60px;
}
input{
  font-size: 1.3rem;
  width: 100%;
  color: coral;
  border-radius: 5px;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: solid black 1px;
  border-radius: 5px;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p{
  padding: 5px;
  text-align: center;
}
.card img{
  max-height: 220px;
}